.post-header {
  padding: 72px 0 0 0;

  h1 {
    font-size: 2.2rem;
    font-weight: 700;
    line-height: 1.2;
    margin-top: 16px;
    word-wrap: break-word;

    @media (max-width: $break-small) {
      font-size: 1.8rem;
    }
  }

  .category {
    text-transform: uppercase;
    display: inline-block;
    font-size: 0.9rem;
    font-weight: 700;
    margin: 10px 0 6px 0;
  }

  .describe {
    font-size: 1.2rem;
    margin-top: 20px;
    color: $describe-color;

    @media (max-width: $break-small) {
      font-size: 1.1rem;
    }
  }

  .date {
    font-size: 1rem;
    margin-top: 24px;
  }

  a {
    color: $primary-color;
    text-decoration: none;
    transition: .2s;
    border-bottom: 2px solid transparent;
  }

  a:hover {
    border-bottom: 2px solid $primary-color;
  }
}

.post-image {
  padding: 72px 0 0 0;

  img {
    width: 100%;
  }
}

.post-contents {
  padding-top: 64px;

  p {
    font-size: 1rem;
    line-height: $wide-line-height;
    margin-bottom: 8px;
    color: black;
    font-weight: 400;
    font-family: $post-font-family;
  }

  a {
    margin-bottom: 8px;
    color: $primary-color;
    font-size: 1rem;
    font-family: $post-font-family;
    transition: 0.3s;
    text-decoration: none;
    border-bottom: 1px solid $primary-color;
  }

  a:hover {
    opacity: 0.5;
  }

  .language-plaintext {
    background-color: #eeeeee;
    color: $primary-color;
    padding: 4px;
    font-size: 0.8rem;
    font-family: $code-font-family;
    font-weight: 400;
    border-radius: 4px;
  }

  img {
    margin: 16px auto;
  }

  .two-divide-content {
    display: grid;
    grid-template-columns: 50% 50%;
    margin-bottom: 8px;

    img {
      height: auto;
      margin: 0 auto;
    }

    @media (max-width: $break-medium) {
      grid-template-columns: 100%;

      img {
        height: 640px;
        width: auto;
      }
    }
  }
}

.tag {
  margin-top: 36px;

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  li {
    display: inline-block;
    margin: 0 8px 14px 0;
    padding: 6px 14px;
    font-size: 0.8rem;
    font-weight: 400;
    font-family: $post-font-family;
    text-transform: uppercase;
    background-color: #f7f8f9;
    border-radius: 96px;
    user-select: none;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    -khtml-user-select: none;

    span {
      color: black;
      opacity: 0.75;
    }
  }

  li::before {
    content: "";
  }
}

.fb-comments, .fb-comments span, .fb-comments iframe {
  width: 100% !important;
}