.hello {
  margin-top: 80px;

  @media (max-width: $break-small) {
    margin-top: 64px;
  }

  h1 {
    font-size: 38px;
    font-weight: 700;
    line-height: 1.2;

    @media (max-width: $break-small) {
      font-size: 32px;
    }
  }

  h2 {
    font-size: 30px;
    font-weight: 400;
    line-height: 1.35;
    margin-top: 36px;

    @media (max-width: $break-small) {
      font-size: 24px;
      margin-top: 24px;
    }
  }
}

.post-slider {
  margin: 64px -12px 0 -12px;

  @media (max-width: $break-medium) {
    margin: 64px -8px 0 -8px;
  }

  .section-header {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    h2 {
      font-size: 1.3rem;
      padding: 0 12px;

      @media (max-width: $break-medium) {
        padding: 0 8px;
      }
    }

    button {
      border: 0;
      width: 36px;
      height: 36px;
      margin: 0 0 0 8px;
      padding: 0;
      outline: none;
      cursor: pointer;
      border-radius: 0;
      border: none;
      -webkit-border-radius: 0;
      -moz-border-radius: 0;
      background-color: transparent;
      background-position: center;
      background-repeat: no-repeat;
      background-size: 24px 24px;
      opacity: 1;
      transition: all .25s ease-in-out;
      -moz-transition: all .25s ease-in-out;
      -webkit-transition: all .25s ease-in-out;
    }

    .slick-prev {
      background-image: url(/assets/svg/left-arrow.svg);
    }

    .slick-next {
      background-image: url(/assets/svg/right-arrow.svg);
    }

    .slick-disabled {
      cursor: not-allowed;
      opacity: 0.15;
      transition: all .25s ease-in-out;
      -moz-transition: all .25s ease-in-out;
      -webkit-transition: all .25s ease-in-out;
    }

    .disabled {
      cursor: not-allowed;
    }
  }

  .slick-slide div {
    outline: none;
  }
}

.article {
  padding: 0 12px;

  @media (max-width: $break-medium) {
    padding: 0 8px;
  }

  .thumbnail {
    width: 100%;
    height: 175px;
    overflow: hidden;
    border-radius: 8px;

    .inner {
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      transition: transform .3s ease-out;
      -webkit-transition: transform .3s ease-out;
      -moz-transition: transform .3s ease-out;
      -o-transition: transform .3s ease-out;
    }

    .inner:hover {
      transform: scale(1.1);
      -ms-transform: scale(1.1);
      -webkit-transform: scale(1.1);
      -o-transform: scale(1.1);
      -moz-transform: scale(1.1);
      border-radius: 8px;
    }
  }

  a {
    text-decoration: none;
    color: $font-color;
  }

  .category {
    display: inline-block;
    font-size: 0.75rem;
    font-weight: 700;
    margin: 10px 0 6px 0;
    text-transform: uppercase;
  }

  h3 {
    font-size: 1.2rem;
    font-weight: 700;
    margin: 0 0 6px 0;
    line-height: 1.4;
  }

  .describe {
    font-size: 0.8rem;
    font-weight: 400;
    color: $describe-color;
  }
}

.articles {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 24px;
  margin: 72px -12px 0 -12px;

  @media (max-width: $break-medium) {
    grid-template-columns: 1fr 1fr;
    margin: 64px -8px 0 -8px;
  }

  @media (max-width: $break-small) {
    grid-template-columns: 1fr;
  }
}