@charset "utf-8";

$base-font-family: "-apple-system", "BlinkMacSystemFont", "Apple SD Gothic Neo", "Montserrat", "noto-sans-cjk-kr", Helvetica, Arial, san-serif !default;
$base-font-weight: 400 !default;
$base-line-height: 1.5 !default;
$base-font-size: 0.95rem !default;
$wide-line-height: 1.75 !default;
$code-font-family: "JetBrains Mono", "-apple-system", "BlinkMacSystemFont", "Apple SD Gothic Neo", "Montserrat", "noto-sans-cjk-kr", Helvetica, Arial, san-serif !default;
$post-font-family:  "-apple-system", "BlinkMacSystemFont", "Apple SD Gothic Neo", "noto-sans-cjk-kr", Helvetica, Arial, san-serif !default;

$break-medium: 720px;
$break-small: 560px;

$primary-color: #383840;
$header-color: #666666;
$font-color: #222222;
$describe-color: #808080;

@mixin box-sizing($type: border-box) {
  -webkit-box-sizing: $type;
  -moz-box-sizing: $type;
  box-sizing: $type;
}

@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  transition: $args;
}

@import "font", "base", "nav", "home", "post";