* {
  box-sizing: border-box;
}

html, body, span, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  line-height: $base-line-height;
  font-family: $base-font-family;
  -webkit-text-size-adjust: 100%;
}

img {
  display: block;
  margin: 0 0 1rem;
  max-width: 100%;
  pointer-events: none;
}

*::selection {
  background-color: $primary-color;
  color: white;
  text-shadow: none;
}

*::-moz-selection {
  background-color: $primary-color;
  color: white;
  text-shadow: none;
}

*::-webkit-selection {
  background-color: $primary-color;
  color: white;
  text-shadow: none;
}

main {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  padding: 0 5%;
}

.contents {
}

.intro-animation {
  animation: fadein 0.75s;
  -moz-animation: fadein 0.75s;
  -webkit-animation: fadein 0.75s;
  -o-animation: fadein 0.75s;
}

@keyframes fadein-dropdown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -10%, 0);
    -ms-transform: translate3d(0, -10%, 0);
    transform: translate3d(0, -10%, 0)
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}