nav {
  color: $primary-color;
  width: 100%;
  background-position: center center;
}

nav .nav-outer {
  position: relative;
  top: 0;
  z-index: 999;
  width: 100%;
  background-color: rgba(255,255,255,.8);
  border-bottom: 1px solid rgba(220,220,220,.8);
  backdrop-filter: blur(20px) saturate(180%);
}

nav .nav-contents {
  display: flex;
  max-width: 1080px;
  width: 100%;
  height: 64px;
  margin: 0 auto;
  padding: 0 5%;
  align-items: center;
  justify-content: space-between;

  ul {
    display: flex !important;
    list-style: none;
    height: 100%;
    padding: 0;

    @media (max-width: $break-medium) {
      display: none !important;
    }
  }

  li {
    vertical-align: middle;
    text-align: center;
    margin: 0;
  }

  div {
    display: flex;
    height: 100%;
    align-items: center;
    margin: 0 0 0 36px;
    border-bottom: 2px solid transparent;
  }

  div:hover {
    border-bottom: 2px solid $header-color;
    transition: 0.3s ease-in-out;
    -webkit-transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
  }

  a {
    display: flex;
    height: 100%;
    text-decoration: none;
    color: $primary-color;
    font-size: 1rem;
    flex-direction: column;
    justify-content: center;
    user-select: none;
  }

  .branding {
    font-weight: 700;
    font-style: italic;
  }
}

.nav-sticky {
  position: fixed;
  top: -68px;
  z-index: 999;
  width: 100%;
  background-color: rgba(255,255,255,.8);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  transition: .3s;
  transition-timing-function: ease;
  transition-delay: 0s;
}

.nav-bar {
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: rgba(220,220,220,.8);
}

.progress-bar {
  position: absolute;
  height: 2px;
  background: $header-color;
  width: 0;
  bottom: 0;
  transition: width 0.2s ease-in-out;
  -webkit-transition: width 0.2s ease-in-out;
  -moz-transition: width 0.2s ease-in-out;
  -o-transition: width 0.2s ease-in-out;
}

.invisible {
  display: none;
}

.menu {
  display: none !important;
  border-bottom: none !important;
  align-items: center;
  cursor: pointer;
  padding: 8px;

  img {
    width: 16px;
    height: 16px;
    margin: 0;
    padding: 0;
  }

  @media (max-width: $break-medium) {
    display: flex !important;
    margin: 0 !important;
  }

  @media (max-width: $break-small) {
    margin: 0 !important;
  }
}

.menu-open {
  max-height: 32px;
}

.menu-close {
}

.mobile-menu {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(245, 245, 245, 0.98);
  z-index: 9999;
  animation: fadein-dropdown 0.3s both 0.3s;
  -moz-animation: fadein-dropdown 0.3s both 0.3s;
  -webkit-animation: fadein-dropdown 0.3s both 0.3s;
  -o-animation: fadein-dropdown 0.3s both 0.3s;

  .close {
    display: flex;
    height: 64px;
    align-items: center;
  }

  .align-right {
    margin-left: auto !important;
  }

  ul {
    margin: 32px 0 0 0;
    padding: 0;
    list-style: none;
  }

  li {
    margin: 24px 0;
    font-size: 1.5rem;
    font-weight: 700;

    a {
      color: $primary-color;
      text-decoration: none;
    }
  }
}